.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #000000;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

h1 {
  font-size: 2em;
  margin: 0.67em 0;
  font-family:monospace;
  color: plum;
}

.fancy {
  font-style: italic;
  font-family: "Times New Roman", Times, serif;
}

.nextButton {
  background-color: plum; /* Green */
  border: none;
  color: rgb(0, 0, 0);
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer;
  border-radius: 12px;
}

p {
  font-size: 1.1em;
  font-family:monospace;
}

.display {
  margin: auto;
}

.invert {
  background-color: rgb(255, 255, 255);
  color: rgb(0, 0, 0);
  width: 30%;
  margin: auto;
}